import React from "react";
import "./Sidebar.css";
import logo from "./logo.svg";
import icon1 from "./icon1.svg";
import icon2 from "./icon2.svg";
import icon3 from "./icon3.svg";
import icon4 from "./icon4.svg";

// GitHub Pages  URL (HashRouter )
const menuItems = [
  {
    label: "HOME",
    children: [
      {
        label: "",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/dashboard/#/user/CustomerMainPage",
      },
    ],
  },
  {
    label: "  ",
    children: [
      {
        label: " ",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/inbound/#/user/CustomerEstimate",
      },
      {
        label: " ",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/inbound/#/user/CustomerContract",
      },
      {
        label: " ",
        icon: icon1,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/inbound/#/user/CustomerInbound",
      },
    ],
  },
  {
    label: "",
    children: [
      {
        label: " ",
        icon: icon2,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/outbound/#/user/CustomerOutboundRequest",
      },
      {
        label: " ",
        icon: icon2,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/outbound/#/user/CustomerOutbound",
      },
    ],
  },
  {
    label: "",
    children: [
      {
        label: "",
        icon: icon3,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/inquiry/#/user/Notices",
      },
      {
        label: "",
        icon: icon4,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/inquiry/#/user/Inquiries",
      },
      {
        label: "",
        icon: icon4,
        url: "https://kjh011012.github.io/wms_project_frontend/customer/inquiry/#/user/Employees",
      },
    ],
  },
];

// Sidebar 
const Sidebar = () => {
  return (
    <div className="sidebar">
      {/*   */}
      <div className="sidebar-header">
        <img src={logo} alt="Logo" className="sidebar-logo" />
        Smart WMS
      </div>

      {/*   */}
      {menuItems.map((menu, index) => (
        <div key={index} className="menu-group">
          <div className="menu-label">{menu.label}</div>
          <div className="menu-children">
            {menu.children.map((child, childIndex) => (
              <a
                key={childIndex}
                className="sidebar-item"
                href={child.url}
                target="_self" // "_blank"   
                rel="noopener noreferrer"
              >
                {child.icon && (
                  <img
                    className="sidebar-icon"
                    src={child.icon}
                    alt={child.label}
                  />
                )}
                <span>{child.label}</span>
              </a>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default Sidebar;
