import React, { useState, useEffect, useRef } from "react";
//import inquiryServer from "../utils/inquiriesServer"; // inquiryServer.js 
import { AgGridReact } from "ag-grid-react";
import "ag-grid-community/styles/ag-grid.css";
import "ag-grid-community/styles/ag-theme-alpine.css";
import "../components/Inquiries.css";
import CommentsPage from "./CommentsPage"; //   import
import mockCustomerInquiries from "./mock/mockCustomerInquiries";

function CustomerInquiries() {
  const [rowData, setRowData] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [selectedInquiry, setSelectedInquiry] = useState(null);
  const [formMode, setFormMode] = useState(null); // 'view' | 'edit' | 'create'
  const [formData, setFormData] = useState({ title: "", content: "" });
  const [showCommentPage, setShowCommentPage] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const gridRef = useRef(null);

  const columnDefs = [
    { headerName: "ID", field: "id", width: 80 },
    { headerName: "", field: "title", flex: 1 },
    { headerName: "", field: "date", width: 150 },
  ];

  //     
  useEffect(() => {
    fetchInquiries();
  }, [searchText]);

  //  
  const fetchInquiries = () => {
  setIsLoading(true);
  try {
    let filtered = [...mockCustomerInquiries];
    if (searchText.trim()) {
      const keyword = searchText.toLowerCase();
      filtered = filtered.filter(
        (item) =>
          item.title.toLowerCase().includes(keyword) ||
          item.content.toLowerCase().includes(keyword) ||
          item.author_email.toLowerCase().includes(keyword)
      );
    }
    setRowData(filtered);
  } catch (err) {
    alert("    .");
  } finally {
    setIsLoading(false);
  }
};


  //   
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  //  
  const openCreatePanel = () => {
    setFormData({ title: "", content: "" });
    setSelectedInquiry(null);
    setFormMode("create");
  };

  //  
  const openViewPanel = (data) => {
    setSelectedInquiry(data);
    setFormData(data);
    setFormMode("view");
  };

  //  
  const openEditPanel = () => {
    setFormMode("edit");
  };

  //  
  const closePanel = () => {
    setFormMode(null);
    setSelectedInquiry(null);
    setFormData({ title: "", content: "" });
    setShowCommentPage(false);
  };

  

  //    
  const handleSave = () => {
  if (!formData.title.trim() || !formData.content.trim()) {
    alert("  .");
    return;
  }

  setIsLoading(true);

  try {
    const payload = {
      ...formData,
      author_email: "user@example.com", //  
      date: new Date().toISOString().slice(0, 10),
    };

    if (formMode === "create") {
      const newId = mockCustomerInquiries.length
        ? Math.max(...mockCustomerInquiries.map((i) => i.id)) + 1
        : 1;

      mockCustomerInquiries.push({ ...payload, id: newId });
      alert(".");
    } else if (formMode === "edit") {
      const index = mockCustomerInquiries.findIndex((i) => i.id === selectedInquiry.id);
      if (index !== -1) {
        mockCustomerInquiries[index] = {
          ...mockCustomerInquiries[index],
          ...payload,
        };
        alert(".");
      }
    }

    fetchInquiries();
    closePanel();
  } catch (err) {
    alert("   .");
  } finally {
    setIsLoading(false);
  }
};

//  
const handleDelete = () => {
  if (!window.confirm("?")) return;

  setIsLoading(true);

  try {
    const index = mockCustomerInquiries.findIndex((i) => i.id === selectedInquiry.id);
    if (index !== -1) {
      mockCustomerInquiries.splice(index, 1);
      alert(".");
    }

    fetchInquiries();
    closePanel();
  } catch (err) {
    alert("   .");
  } finally {
    setIsLoading(false);
  }
};



  const onRowClicked = (event) => openViewPanel(event.data);

  if (showCommentPage && selectedInquiry) {
    return <CommentsPage inquiryId={selectedInquiry.id} onBack={closePanel} />;
  }

  return (
    <div className="inquiries-container">
      <div className="search-bar">
        <input
          type="text"
          placeholder=" "
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
        <button onClick={fetchInquiries} disabled={isLoading}>
          
        </button>
        <button onClick={openCreatePanel} style={{ float: "right" }}>
           
        </button>
      </div>

      <div className="ag-theme-alpine grid-container">
        <AgGridReact
          ref={gridRef}
          rowData={rowData}
          columnDefs={columnDefs}
          rowSelection="single"
          domLayout="autoHeight"
          onRowClicked={onRowClicked}
        />
      </div>

      {formMode && (
        <div className="overlay" onClick={closePanel}>
          <div className="details-panel" onClick={(e) => e.stopPropagation()}>
            <h3>{formMode === "create" ? " " : " "}</h3>
            <label></label>
            <input
              type="text"
              name="title"
              value={formData.title}
              onChange={handleInputChange}
              disabled={formMode === "view"}
            />
            <label></label>
            <textarea
              name="content"
              value={formData.content}
              onChange={handleInputChange}
              disabled={formMode === "view"}
            ></textarea>

            {formMode === "view" && (
              <>
                <button onClick={openEditPanel}></button>
                <button onClick={handleDelete}></button>
                <button onClick={() => setShowCommentPage(true)}> </button>
              </>
            )}
            {(formMode === "create" || formMode === "edit") && (
              <button onClick={handleSave} disabled={isLoading}>
                {formMode === "create" ? "" : ""}
              </button>
            )}
            <button onClick={closePanel}></button>
          </div>
        </div>
      )}
    </div>
  );

}

export default CustomerInquiries;
